/**
 * Styles for Circle Grid sections.
 */

.section-circle-grid {
	position: relative; // So we can position the :before pseudo-element.
	background-color: $deep-blue;
	color: $white;
	text-align: center;
	line-height: rem(28);
	z-index: -1;

	@include breakpoint( $tablet ) {
		background-color: transparent;

		&:before {
			content: '';
			position: absolute;
			top: rem(125);
			right: 0;
			bottom: 0;
			left: 0;
			background-color: $deep-blue;
		}
	}

	.grid-item {
		position: relative; // So these are rendered on top of .section-circle-grid:before.
		margin-top: 0;
		margin-bottom: rem(50);

		@include breakpoint( $tablet ) {
			width: rem(250);
			margin-left: rem(55);
			margin-right: rem(55);
			padding: 0;
		}

		img {
			display: block;
			margin: 0 auto rem(30);
			border-radius: 50%;
		}
	}

	a {
		color: $white;
		text-decoration: none;
	}

	h3,
	p {
		margin: rem(10) 0;
	}

	h3 {
		font-size: rem(16);
	}

	.grid-item-blurb {
		font-weight: bold;
	}

	.grid-item-heading ~ .grid-item-blurb {
		font-weight: normal;
	}
}
